<template>
  <div class="zizhushegnbao">
    <meta
      name="viewport"
      content="width=device-width,initial-scale=1.0, minimum-scale=1.0, maximum-scale=1.0, user-scalable=no"
    />
    <!-- <van-form
      @submit="onSubmit"
      @failed="onFailed"
      validate-first
      show-error-message
      scroll-to-error
    > -->
    <div class="title">
      <van-button
        style="background-color: #565353 !important; border: 0"
        to="/"
        icon="arrow-left"
        type="primary"
      /><span style="margin: 0 auto">外出申报</span>
    </div>
    <div class="main" v-show="!show">
      <van-field
        v-model="formValute.real_name"
        class="zizhushengbaoFiled"
        placeholder="请输入姓名"
        label-class="zizhushengbaoLabel"
        label="姓名："
      />
      <van-field
        label-class="zizhushengbaoLabel"
        v-model="formValute.card_type_text"
        placeholder="选择"
        class="zizhushengbaoFiled"
        @click="showPicker_idCardType = true"
        readonly
        right-icon="arrow"
        label="证件类型："
      />
      <van-field
        v-model="formValute.id_card"
        class="zizhushengbaoFiled"
        @click="eventIfSelect"
        @input="
          () => {
            this.formValute.id_card = this.formValute.id_card.replace(
              /[^\w\.\/]/g,
              ''
            );
          }
        "
        :readonly="!formValute.card_type_text"
        label-class="zizhushengbaoLabel"
        :rules="[{ validator: asyncValidator, message: '请输入正确的证件号' }]"
        :placeholder="
          formValute.card_type_text === ''
            ? '请先选择证件号类别'
            : '请输入正确的证件号'
        "
        label="证件号："
      />
      <van-field
        v-model="formValute.phone"
        class="zizhushengbaoFiled"
        label-class="zizhushengbaoLabel"
        center
        type="number"
        placeholder="请输入手机号"
        clearable
        label="手机号："
      >
      </van-field>
      <van-field
        v-model="formValute.sms_code"
        class="zizhushengbaoFiled"
        label-class="zizhushengbaoLabel"
        center
        :placeholder="
          formValute.phone === '' ? '请先输入手机号' : '请输入验证码'
        "
        :readonly="!formValute.phone"
        clearable
        label="验证码："
      >
        <template #button>
          <van-button size="small" type="primary" @click="getsms" v-if="!smsIF"
            >获取验证码</van-button
          >
          <van-button size="small" type="primary" class="gray" disabled v-else
            ><span>{{ smsdJS }}后重新获取</span></van-button
          >
        </template>
      </van-field>
      <van-field
        label-class="zizhushengbaoLabel"
        placeholder="(省、市、县选择)"
        class="zizhushengbaoFiled"
        @click="showPicker = true"
        type="textarea"
        rows="1"
        autosize
        v-model="addAdress"
        readonly
        label="目的地："
      />
      <!-- <div style="color: red; margin-bottom: 1rem">
        您选择了义乌作为出行目的地，确定要提交吗？
      </div> -->
      <van-field
        v-model="formValute.leave_time"
        class="zizhushengbaoFiled"
        label-class="zizhushengbaoLabel"
        @click="showPickerDate = true"
        readonly
        label="离义时间："
      />
      <van-field
        v-model="formValute.expect_return_time"
        class="zizhushengbaoFiled"
        label-class="zizhushengbaoLabel"
        @click="showPickerDate_fh = true"
        placeholder="请选择预计返义时间"
        readonly
        label="预计返义时间："
      >
        <template #label>
          <span style="width: 6.875rem; display: block"> 预计返义时间： </span>
        </template>
      </van-field>
      <van-popup v-model="showPicker" round position="bottom">
        <van-cascader
          v-model="cascaderValue"
          title="请选择所在地区"
          :field-names="{
            text: 'name',
            value: 'id',
            children: 'children',
          }"
          :options="columns"
          @change="onChange_zhengjie"
          @close="showPicker = false"
          @finish="onConfirm_zhengjie"
        />
      </van-popup>
      <van-popup v-model="showPickerDate" round position="bottom">
        <van-datetime-picker
          v-model="currentDate"
          type="date"
          title="选择离义时间"
          @confirm="onConfirm_Date"
          @cancel="showPickerDate = false"
        />
      </van-popup>
      <van-popup v-model="showPickerDate_fh" round position="bottom">
        <van-datetime-picker
          v-model="fnCurrentDate"
          type="date"
          title="选择返义时间"
          :min-date="minDate"
          @confirm="onConfirm_Date_leave"
          @cancel="showPickerDate_fh = false"
        />
      </van-popup>
      <van-popup v-model="showPicker_idCardType" round position="bottom">
        <van-picker
          title="证件类型"
          show-toolbar
          :columns="idCardTypeColums"
          @confirm="onConfirm_idCardType"
          @cancel="onCancel_idCardType"
        />
      </van-popup>
      <van-dialog
        v-model="showPickerUploader"
        title="点击下面的方框选择行程码"
        show-cancel-button
      >
        <!--           :max-size="5000000"
          @oversize="onOversize" -->
        <van-uploader
          v-model="fileList"
          max-count="1"
          :after-read="afterRead"
        />
        <!-- <img src="https://img01.yzcdn.cn/vant/apple-3.jpg" /> -->
      </van-dialog>
      <van-button
        type="primary"
        @click="onSubmit"
        block
        class="submit"
        native-type="submit"
        >提交</van-button
      >
    </div>
    <div v-show="false">{{ msg }}</div>
    <!-- </van-form> -->
    <van-overlay :show="overlay">
      <van-loading
        size="24px"
        vertical
        style="
          position: fixed;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        "
        >正在校验姓名和身份证号码</van-loading
      >
    </van-overlay>
    <success
      :dataList="formValute"
      class="fixedPOstion"
      :dataList_text="formValute_text"
      :addAdress="addAdress"
      :id_verify_result="id_verify_result"
      v-if="show"
      getRoute="risingFalling"
      @closeSparingContent="closeSparingContent($event)"
      @Back="back($event)"
    ></success>
  </div>
</template>

<script>
import VDistpicker from 'v-distpicker'
import { areaList } from '@vant/area-data'
import {
  Toast
  , Dialog
} from 'vant'
import
success
  from '../components/success.vue'

import {
  getAreaUserApi, POSTOwndeLeaveYiwuApi, getSmsCode, uploadImg
} from '@/common/api/api.js'
import { mixinsChaXun } from '../common/mixinsChaXun'
import Vue from 'vue'
export default {
  components: { VDistpicker, success },
  mixins: [mixinsChaXun],
  data () {
    return {
      risingFallingType: 'risfingll',
      areaList,
      cascaderValue: '',
      columns: [

      ],
      flag: false,
      loading: true,
      isHide: false,
      showDiagler: false,
      smsCode: '',
      fnCurrentDate: '', // 预计返义
      minDate: new Date(2020, 0, 1),
      maxDate: new Date(2025, 10, 1),
      show: false,
      currentDate: new Date(2021, 0, 17),
      idCardTypeColums: [
        {
          value: 'id',
          text: '身份证'
        },
        {
          value: 'passport',
          text: '护照号'
        },
        {
          value: 'officer',
          text: '军官证号'
        }
      ],
      showPickerUploader: false,
      showPicker: false,
      showPicker2: false,
      showPickerDate: false,
      showPickerDate_fh: false,
      showPicker_idCardType: false,
      fileList: [],
      formValute: {
        phone: '',
        province_id: '',
        city_id: '',
        card_type_text: '身份证',
        card_type: 'id',
        street_id: '',
        sms_code: '',
        county_id: ''
      },
      formValute_text: {
        province: '',
        city: '',
        street: '',
        county: ''
      },
      checked: false,
      value: '',
      sms: '',
      smsIF: false,
      setTime: '',
      columns2: [],
      smsdJS: 60,
      newData: [],
      acData_second: 0,
      acData: 0,
      msg: 1
    }
  },
  created () {
    // 获取全国4级联动
    this.getAreaUserApi()
    this.addDate()
  },
  computed: {
    addAdress () {
      return this.formValute_text.province + this.formValute_text.city + this.formValute_text.county + this.formValute_text.street
    }
  },
  methods: {
    addDate () {
      const nowDate = new Date()
      const date = {
        year: nowDate.getFullYear(),
        month: nowDate.getMonth() + 1,
        date: nowDate.getDate()
      }
      const newmonth = date.month > 10 ? date.month : '0' + date.month
      const day = date.date >= 10 ? date.date : '0' + date.date
      this.formValute.leave_time = date.year + '-' + newmonth + '-' + day
      this.minDate = new Date(this.formValute.leave_time)
      this.currentDate = new Date(this.formValute.leave_time)
    },
    onSubmit () {
      let formValute = this.formValute
      if (!formValute.real_name) {
        Toast.fail('请输入姓名')
        return false
      }
      var pat = new RegExp('[^a-zA-Z0-9\_\u4e00-\u9fa5]', 'i') // 特殊字符
      let realNameChilaReg = /\d+|\w+/ // 字母数字
      if (pat.test(formValute.real_name)) {
        console.log(formValute.real_name, pat.test(formValute.real_name))
        Toast.fail('姓名不能包含特殊字符')
        return false
      }
      if (this.formValute.card_type === 'id') {
        if (realNameChilaReg.test(formValute.real_name)) {
          Toast.fail('姓名不能包含字母或数字')
          return false
        }
      }
      if (!formValute.card_type) {
        Toast.fail('请选择证件号类别')
        return false
      }
      if (!formValute.id_card) {
        Toast.fail('请输入证件号')
        return false
      }
      let idCardReg = /^[1-9]\d{5}(18|19|20)\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$/// 先用正则判断是否为正确的格式
      if (this.formValute.card_type === 'id') {
        if (!idCardReg.test(formValute.id_card)) {
          Toast.fail('证件号格式错误')
          return false
        } else {
          console.log('校验')
        }
      }
      if (!formValute.phone) {
        Toast.fail('请输入手机号')
        return false
      }
      if (!formValute.sms_code) {
        Toast.fail('请输入验证码')
        return false
      }
      let myreg = /^1\d{10}$/
      if (!myreg.test(formValute.phone)) {
        Toast.fail('手机号格式错误')
        return false
      }
      if (!formValute.province_id) {
        Toast.fail('请选择目的地')
        return false
      }
      if (!formValute.leave_time) {
        Toast.fail('请选择离义时间')
        return false
      }
      if (!formValute.expect_return_time) {
        Toast.fail('请选择预计返义时间')
        return false
      }
      // this.show = true
      // this.isHide = true
      // 若为义乌
      const that = this
      if (formValute.county_id === 2832) {
        Dialog.confirm({
          message: '您选择了义乌作为出行目的地，确定要提交吗？'
        })
          .then(() => {
            // on confirm
            that.yanzhengOther()
          })
          .catch(() => {
            return false
            // on cancel
          })
      } else {
        that.yanzhengOther()
      }
    },
    yanzhengOther () {
      if (this.formValute.card_type === 'id') {
        this.qgrkk({
          real_name: this.formValute.real_name,
          id_card: this.formValute.id_card
        })
      } else {
        this.id_verify_result = 0
        this.show = true
        this.isHide = true
      }
    },
    closeSparingContent (show) {
      this.show = show
      this.isHide = false
    },
    onFailed () { },
    onConfirm_idCardType (idCardType) {
      console.log(idCardType, 'SA')
      this.formValute.card_type = idCardType.value
      this.formValute.card_type_text = idCardType.text
      this.showPicker_idCardType = false
    },
    onCancel_idCardType () {

    },
    getAreaUserApi (selectedOptionsObject) {
      getAreaUserApi(selectedOptionsObject).then(res => {
        console.log(res.data)
        if (selectedOptionsObject && selectedOptionsObject.level === 2) {
          this.newData.children[this.acData_second].children.map(item => {
            if (res.data.length <= 0) {
              delete item.children
            }
          })
          console.log(res.data)
        }
        this.loading = false
        if (selectedOptionsObject) {
          const _this = this
          switch (selectedOptionsObject.level) {
            case 0:
              this.acData = _this.columns.findIndex(x => x.id === selectedOptionsObject.pid)
              console.log(this.acData)
              this.columns[this.acData].children = res.data
              this.newData = JSON.parse(JSON.stringify(this.columns[this.acData]))
              console.log(this.newData.children, '1级联动')
              this.newData.children.length !== 0
                ? this.newData.children.map(item => {
                  if (item) {
                    item.children = []
                  }
                }) : this.showPicker = false
              this.newData.children.map(item => {
                if (item) {
                  item.children = []
                }
              })
              console.log(this.newData.children.length)
              this.columns.splice(this.acData, 1, this.newData)
              break
            case 1:
              // newData.children = res.data
              console.log(this.newData.children, '2级联动')
              this.acData_second = this.newData.children.findIndex(x => x.id === selectedOptionsObject.pid)
              this.newData.children[this.acData_second].children = res.data
              this.columns.splice(this.acData, 1, this.newData)
              this.newData.children[this.acData_second].children.map(item => {
                item.children = []
              })
              break
            case 2:
              let newDataTwo = this.newData.children[this.acData_second].children
              let acDatathird = newDataTwo.findIndex(x => x.id === selectedOptionsObject.pid)
              if (res.data.length > 0) {
                newDataTwo[acDatathird].children = res.data
              }
              this.columns.splice(this.acData, 1, this.newData)
              break
            default:
              break
          }
          // this.$nextTick(() => {
          //   this.$forceUpdate()
          // })
        } else {
          this.columns = res.data
          this.columns.map(item => {
            if (item.name !== '台湾') {
              item.children = []
            }
          })
        }
      })
    },
    getsms () {
      getSmsCode({
        prefix: 'declare_verify',
        phone: this.formValute.phone
      }).then(res => {
        if (res.code === 200) {
          this.smsIF = true
          this.showDiagler = true
          this.djsSms()
          Toast.success(res.msg)
        } else {
          Toast.fail(res.msg)
        }
      })
    },
    djsSms () {
      this.smsdJS = this.smsdJS - 1
      this.smsIF = true
      if (this.smsdJS <= 0) {
        this.smsIF = false
        this.smsdJS = 60
        clearTimeout(this.setTime)
      } else {
        this.setTime = setTimeout(() => {
          this.djsSms()
        }, 1000)
      }
    },
    afterRead (file) {
      console.log(file)
      uploadImg({
        file: file
      }).then(res => {

      })
    },
    onConfirm_zhengjie (zhengjie) {
      console.log(zhengjie, 'zhengjie')
      // this.formValute.zhengjie = zhengjie
      let selectList = zhengjie.selectedOptions
      selectList.map((item, index) => {
        this.formValute.province_id = selectList[0].id
        this.formValute.city_id = selectList[1].id
        this.formValute.county_id = selectList[2].id
        if (selectList[3]) {
          this.formValute.street_id = selectList[3].id
          this.formValute_text.street = selectList[3].name
        } else {
          this.formValute.street_id = ''
          this.formValute_text.street = ''
        }
        this.formValute_text.province = selectList[0].name
        this.formValute_text.city = selectList[1].name
        this.formValute_text.county = selectList[2].name
      })
      this.showPicker = false
    },
    onChange_zhengjie (value) {
      let selectList = value.selectedOptions
      selectList.map((item, index) => {
        if (selectList[0]) {
          this.formValute.province_id = selectList[0].id
          this.formValute_text.province = selectList[0].name
        } else {
          this.formValute.province_id = ''
          this.formValute_text.province = ''
        }
        if (selectList[1]) {
          this.formValute.city_id = selectList[1].id
          this.formValute_text.city = selectList[1].name
        } else {
          this.formValute.city_id = ''
          this.formValute_text.city = ''
        }
        if (selectList[2]) {
          this.formValute.county_id = selectList[2].id
          this.formValute_text.county = selectList[2].name
        } else {
          this.formValute.county_id = ''
          this.formValute_text.county = ''
        }
        if (selectList[3]) {
          this.formValute.street_id = selectList[3].id
          this.formValute_text.street = selectList[3].name
        } else {
          this.formValute.street_id = ''
          this.formValute_text.street = ''
        }
      })
      console.log(value)
      let selectedOptionsObject = value.selectedOptions[value.selectedOptions.length - 1]
      this.getAreaUserApi({
        // eslint-disable-next-line no-use-before-define
        pid: selectedOptionsObject.id,
        level: selectedOptionsObject.level
      })
    },
    onCancel_zhengjie () {
      this.showPicker = false
    },
    choose () {
      this.show = !this.show
    },
    onChangeProvince (a) {
      console.log(a)
      this.formValute.laiyuandi.province = a
    },
    onConfirm_Date (date) {
      this.showPickerDate = false
      this.formValute.leave_time = this.$dateTimeFilters(date, 'yyyy-MM-dd')
    },
    onConfirm_Date_leave (date) {
      this.showPickerDate_fh = false
      this.formValute.expect_return_time = this.$dateTimeFilters(date, 'yyyy-MM-dd')
    },
    asyncValidator (val) {
      if (this.formValute.card_type_text === '身份证') {
        return /^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/.test(val)
      } else {
        return true
      }
    },
    eventIfSelect () {
      // 若选择了证件类型
      if (this.formValute.card_type_text) {

      } else {
        Toast.fail('请先选择证件类型')
      }
    },
    back () {
      this.$router.push({
        path: '/'
      })
    }
  }
}
</script>
<style>
.zizhushegnbao ::-webkit-input-placeholder {
  color: red !important;
  font-weight: 500 !important;
}

/* Firefox 4-18 */
.zizhushegnbao :-moz-placeholder {
  color: red !important;
  font-weight: 500 !important;
}

/* Firefox 19-50 */
.zizhushegnbao ::-moz-placeholder {
  color: red !important;
  font-weight: 500 !important;
}
.zizhushengbaoLabel_other {
  /* color: red !important; */
  width: 8.5rem;
}
/* - Internet Explorer 10–11
- Internet Explorer Mobile 10-11 */
.zizhushegnbao :-ms-input-placeholder {
  color: red !important;
  font-weight: 500 !important;
}

/* Edge (also supports ::-webkit-input-placeholder) */
.zizhushegnbao ::-ms-input-placeholder {
  color: red !important;
  font-weight: 500 !important;
}

/* CSS Working Draft */
.zizhushegnbao ::placeholder {
  color: red !important;
  font-weight: 500 !important;
}
.distpicker-address-wrapper .address-container ul li.active {
  color: red !important;
}
.distpicker-address-wrapper .address-container ul li {
  font-size: 14px;
}
.van-dialog__content {
  display: flex;
  justify-content: center;
}
.van-uploader__upload {
  width: 10rem;
  height: 10rem;
}
.distpicker-address-wrapper {
  color: black;
}
.cellClass_spring > .van-cell__value {
  min-width: 75%;
}
.van-field__control {
  font-size: 14px !important;
}
.van-nav-bar__right {
  padding: 5px;
}
.van-tabs--card > .van-tabs__wrap {
  height: 36px;
}
.van-tabs__nav--card {
  margin: 0;
  border: none;
  height: 36px;
}
.van-tabs__nav--card .van-tab {
  color: #333333;
  border-right: none;
  height: 36px;
}
.van-tabs__nav--card .van-tab.van-tab--active {
  color: #fff;
  background-color: #ff5318;
}
.zizhushengbaoLabel {
  /* color: red !important; */
}
.van-field__label {
  width: auto;
}
input:disabled {
  background-color: #d5d5d5 !important;
}
</style>
<style  scoped>
textarea {
  text-align: right;
}
.van-form {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.submit {
  margin-top: 1.8rem;
  border-radius: 0.3125rem;
}
.title {
  font-size: 18px;
  width: 95%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: #fff;
  margin-bottom: 1rem;
}
.zizhushegnbao {
  min-height: 100vh;
  background: #565353;
  display: flex;
  /* justify-content: center; */
  align-items: center;
  flex-direction: column;
  padding: 0.9375rem;
}
.zizhushengbaoFiled {
  border-radius: 0.375rem;
  background-color: #d5d5d5;
  /* width: 95%; */
  display: flex;
  align-items: center;
  height: 2.8125rem;
  margin-bottom: 0.9375rem;
}
.canYouHandBaoGao {
  color: #68b0f9;
  font-size: 1rem;
  margin-right: 2rem;
}
.dibu {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
}
.divwrap {
  height: 400px;
  overflow-y: auto;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
}
.divwrap >>> .distpicker-address-wrapper {
  color: #999;
}
.divwrap >>> .address-header {
  position: fixed;
  bottom: 400px;
  width: 100%;
  background: #000;
  color: #fff;
}
.divwrap >>> .address-header ul li {
  flex-grow: 1;
  text-align: center;
}
.divwrap >>> .address-header .active {
  color: #fff;
  border-bottom: #666 solid 8px;
}
.divwrap >>> .address-container .active {
  color: #000;
}
.pickerUploderPopup {
  width: 80%;
  height: 30%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: rgb(225, 226, 225);
}
.fixedPOstion {
  position: absolute;
  top: 0;
  left: 0;
  /* min-height: 100vh; */
  /* height: 100%; */
  background: #565353;
  width: 100%;
  padding: 0 0.5rem;
  box-sizing: border-box;
  padding-top: 0.3125rem;
}
</style>



