<template>
  <div>
    <meta
      name="viewport"
      content="width=device-width,initial-scale=1.0, minimum-scale=1.0, maximum-scale=1.0, user-scalable=no"
    />
    <div class="" style="margin-top: 2.75rem">
      <div class="sureCellGroup">
        <van-cell-group class="sureCellGroup" inset>
          <van-cell title="姓名" :value="dataList.real_name" />
          <van-cell title="证件类型" :value="dataList.card_type_text" />
          <van-cell title="证件号" :value="dataList.id_card" />
          <van-cell title="手机号" :value="dataList.phone" />
          <van-cell
            title="来源地"
            title-class="titleClass"
            v-if="getRoute === 'declarationBayonet'"
            :value="addAdress"
          >
          </van-cell>
          <van-cell
            title="来源地"
            title-class="titleClass"
            v-if="
              getRoute === 'zizhushengbaoWeixian' ||
              getRoute === 'goAndBackYiwu'
            "
            style="padding-right: 0"
            :value="addAdress"
          >
            <template #default>
              <!-- <van-field
                input-align="right"
                v-model="addAdress"
                rows="1"
                readonly
                autosize
                type="textarea"
              /> -->
              <van-field v-model="message" rows="1" autosize type="textarea" />
            </template>
          </van-cell>
          <van-cell
            title="目的地"
            v-if="getRoute === 'risingFalling'"
            :value="addAdress"
          >
            <template #default>
              <van-field
                v-model="addAdress"
                input-align="right"
                readonly
                rows="1"
                autosize
                style="text-align: right"
                type="textarea"
              />
            </template>
          </van-cell>
          <van-cell
            title="来义后居所"
            v-if="getRoute !== 'risingFalling'"
            :value="dataList_text.yw_street"
          />
          <van-cell
            title="来义时间"
            v-if="
              getRoute === 'zizhushengbaoWeixian' ||
              getRoute === 'goAndBackYiwu'
            "
            :value="dataList.arrival_time"
          />
          <van-cell
            title="离义时间"
            v-if="getRoute === 'risingFalling'"
            :value="dataList.leave_time"
          />
          <van-cell
            title="预计返义时间"
            v-if="getRoute === 'risingFalling'"
            :value="dataList.expect_return_time"
          />
          <van-cell
            title="离开中高风险地区时间"
            v-if="getRoute === 'zizhushengbaoWeixian'"
            :value="dataList.leave_riskarea_time"
          />
          <van-cell
            title="来义交通工具"
            v-if="getRoute === 'zizhushengbaoWeixian'"
            :value="dataList_text.arrival_transport_mode"
          />
          <van-cell
            title="车次/航班/车牌"
            v-if="getRoute === 'zizhushengbaoWeixian'"
            :value="dataList.arrival_sign"
          />
          <van-cell
            title="详细地址"
            v-if="
              getRoute === 'zizhushengbaoWeixian' ||
              getRoute === 'goAndBackYiwu'
            "
            style="padding-right: 0"
            :value="dataList.address"
          >
            <template #default>
              <van-field
                input-align="right"
                v-model="dataList.address"
                rows="1"
                readonly
                autosize
                type="textarea"
              />
            </template>
          </van-cell>
          <van-cell
            title="行程码"
            v-if="
              getRoute !== 'risingFalling' && getRoute !== 'declarationBayonet'
            "
          >
            <template #default>
              <img
                fit="cover"
                style="width: 5rem; height: 5rem"
                v-if="fileList[0] && fileList[0].status === 'done'"
                :src="fileList[0].content"
              />
            </template>
          </van-cell>
        </van-cell-group>
      </div>
      <van-checkbox
        v-model="checked"
        @change="changeCheckBox"
        class="checkBox"
        icon-size="16px"
      >
        <div
          style="
            color: red;
            text-align: center;
            height: 32px;
            display: flex;
            align-items: center;
            margin-top: 5px;
          "
        >
          本人承诺所填写的资料真实有效
        </div>
      </van-checkbox>
      <div class="submitBox">
        <van-button class="submitBoxBtn submitBoxBtn_left" @click="goBack"
          >返回修改</van-button
        >
        <van-button
          class="submitBoxBtn submitBoxBtn_right"
          v-if="checked"
          :disabled="isDisabled"
          :loading="isDisabled"
          loading-type="spinner"
          @click="onSubmit"
          loading-text="提交中..."
          >确定提交</van-button
        >
        <van-button
          class="submitBoxBtn submitBoxBtn_right"
          v-else
          :disabled="!checked"
          :loading="isDisabled"
          loading-type="spinner"
          @click="onSubmit"
          loading-text="提交中..."
          >确定提交</van-button
        >
      </div>
    </div>
    <van-popup
      v-model="showDiaglo"
      class="vanPopupIt"
      @close="back"
      lock-scroll
      :close-on-click-overlay="false"
      closeable
      close-icon-position="top-right"
      get-container="#app"
    >
      <div class="borderImage">
        <div class="showQrTk">
          <van-image
            width="80"
            height="80"
            class="van_Image"
            :src="require('../assets/img/tj.png')"
          />
          <div style="font-size: 1.125rem; color: #333333; font-weight: bold">
            提交成功
          </div>
          <div class="lineHenxian"></div>
          <div class="content" v-if="getRoute === 'risingFalling'">
            感谢您的申请，祝您出行愉快，请在出行期间做好个人防护，避免去中高风险地区，返义后请及时申报。
          </div>
          <div class="content" v-else>
            欢迎来到世界小商品之都义乌！
            温馨提示：请您抵义后2小时内在全程做好个人防护的情况下及时主动向所在社区和单位（或所住酒店）报告，并配合做好信息核查、健康管理等工作。如有疑问，请拨打24小时值班电话0579-85532860、0579-85539123。疫情防控，需要您我共同努力！
          </div>
        </div>
      </div>
    </van-popup>
  </div>
</template>

<script>
import {
  POSTOwndecomeBackApi, POSTOwndeLeaveYiwuApi, POSTRiskWEIxianApi, POSTDeclarationBayonet
} from '@/common/api/api.js'
import {
  Toast
} from 'vant'
export default {
  props: {
    getRoute: {
      type: String,
      default: () => ''
    },
    addAdress: {
      type: String,
      default: () => ''
    },
    dataList: {
      type: Object,
      default: () => {
        return {}
      }
    },
    dataList_text: {
      type: Object,
      default: () => {
        return {}
      }
    },
    fileList: {
      type: Array,
      default: () => {
        return []
      }
    },
    id_verify_result: {
      type: Number,
      default: () => {
        return 0
      }
    }
  },
  watch: {
    dataList: {
      handler (newValue, oldValue) {
        this.formValue = newValue
        this.$forceUpdate()
      },
      deep: true
    },
    dataList_text: {
      handler (newValue, oldValue) {
        this.formValueText = newValue
        this.$forceUpdate()
      },
      deep: true
    }
  },
  data () {
    return {
      checked: false,
      password: '',
      formValue: this.dataList,
      formValueText: this.dataList_text,
      isDisabled: false,
      showDiaglo: false
    }
  },
  methods: {
    back () {
      this.isDisabled = false
      this.$emit('Back', false)
    },
    onSubmit () {
      this.isDisabled = true
      const formValueResult = JSON.parse(JSON.stringify(this.dataList))
      formValueResult.city = this.dataList_text.city
      formValueResult.province = this.dataList_text.province
      formValueResult.county = this.dataList_text.county
      formValueResult.street = this.dataList_text.street
      formValueResult.yw_street = this.dataList_text.yw_street
      formValueResult.id_verify_result = this.id_verify_result
      switch (this.getRoute) {
        case 'goAndBackYiwu':
          POSTOwndecomeBackApi(formValueResult).then(res => {
            if (res.code !== 200) {
              Toast.fail(res.msg)
              this.isDisabled = false
            } else {
              this.showDiaglo = true
            }
          })
          break
        case 'risingFalling':
          POSTOwndeLeaveYiwuApi(formValueResult).then(res => {
            if (res.code !== 200) {
              this.isDisabled = false
              Toast.fail(res.msg)
            } else {
              this.showDiaglo = true
            }
          })
          break
        case 'zizhushengbaoWeixian':
          POSTRiskWEIxianApi(formValueResult).then(res => {
            if (res.code !== 200) {
              Toast.fail(res.msg)
              this.isDisabled = false
            } else {
              this.showDiaglo = true
            }
          })
          break
        case 'declarationBayonet':
          delete formValueResult.id_verify_result
          delete formValueResult.province
          delete formValueResult.street
          delete formValueResult.county
          formValueResult.city = this.dataList.city
          formValueResult.barrier = 'barrier'
          POSTDeclarationBayonet(formValueResult).then(res => {
            if (res.code !== 200) {
              Toast.fail(res.msg)
              this.isDisabled = false
            } else {
              this.showDiaglo = true
            }
          })
          break
        default:
          break
      }
    },
    goBack () {
      this.$emit('closeSparingContent', false)
    }
  }
}
</script>

<style>
.van-cell__value--alone {
  text-align: right;
}
.van-cell__value {
  /* min-width: 70%; */
}
.sureSubmit {
  min-height: 100vh;
  background: #565353;
  width: 100%;
  padding: 0 15rem;
  box-sizing: border-box;
  padding-top: 1.3125rem;
  position: fixed;
  left: 0;
  top: 0;
}
.sureCellGroup {
  border-radius: 0.5rem !important;
  box-shadow: 0px 0px 0.5rem 1px rgba(12, 12, 12, 0.11);
  /* width: 22.1875rem; */
  /* width: ; */
  /* height: 18.75rem; */
  overflow-x: hidden;
  background: #ffff;
}
.submitBox {
  flex-direction: row;
  justify-content: center;
  display: flex;
  /* position: relative; */
  /* bottom: rem; */
  margin: 1rem 0;
  /* width: 22.5rem; */
}
.submitBoxBtn {
  height: 2.25rem !important;
  width: 22.5rem !important;
  border-radius: 0.25rem !important;
  color: #b33301 !important;
}
.submitBoxBtn_left {
  background: linear-gradient(-90deg, #fae4b3 0%, #faf7f1 100%) !important;
  border: none;
  margin-right: 1.25rem !important;
}
.submitBoxBtn_right {
  background: linear-gradient(-90deg, #fa5c00 0%, #fa9200 100%) !important;
  border: none;
}
.vanPopupIt {
  border-radius: 1rem;
  width: 16.0625rem;
  border: 0.1875rem solid #f6b300;
  background: #f0f0f0;
  /* position: relative; */
}
.showQrTk {
  padding: 1.25rem;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.van_Image {
  margin-top: 2.3125rem;
  margin-bottom: 1.25rem;
}
.lineHenxian {
  width: 13.0625rem;
  height: 0.0938rem;
  background: linear-gradient(
    #ffffff 0%,
    rgba(250, 250, 250, 0.99) 50%,
    #ffffff 100%
  );
  margin-top: 1.5625rem;
}
.content {
  margin-top: 1.5625rem;
  width: 13.625rem;
  font-size: 0.875rem;
  line-height: 1.25rem;
  letter-spacing: 1px;
  color: #333;
  text-indent: 1em;
  text-align: justify;
  /* height: 4.4375rem; */
}
.van-cell {
  display: flex;
  align-items: center;
  /* height: 44px; */
}
.checkBox {
  display: flex;
  align-items: center;
  height: 2rem;
}
</style>



